import { mapState } from 'vuex'
import store from '@/store'

// store/index.jsvuexstate
let $tStoreKey = []
try {
  $tStoreKey = store.state ? Object.keys(store.state) : []
} catch(e) {
  
}

module.exports = {
  beforeCreate() {
    // vuex$t
    // : 
    // vuexstateuser.name => this.$tn.vuex('user.name', '')
    // vuexde stateversion1.0.1 => this.$tn.vuex('version', 1.0.1)
    this.$tn.vuex = (name, value) => {
      this.$store.commit('$tStore', {
        name, value
      })
    }
  },
  computed: {
    // vuexstatemixin
    ...mapState($tStoreKey)
  }
}